import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {User} from '../../models/user';
import {AngularFireAuth} from 'angularfire2/auth';

@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {

  user = {} as User;

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private alertCtrl: AlertController, private afAuth: AngularFireAuth) {
  }

  async register(user: User) {
    this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.password).then(
      () => {
        this.showAlert('Suksess', 'Ny bruker opprettet.');
        this.afAuth.authState.subscribe(data => {
          data.sendEmailVerification();
        });
        this.navCtrl.pop();
      }, e => {
        this.showAlert('Error', e);
      }
    );
  }

  private showAlert(title, message) {
    const alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: [
        {
          text: 'Ok',
          handler: () => {
            return;
          }
        }
      ]
    });
    alert.present();
  }
}



